import { StatisticsService } from './../statistics/statistics.service';
import { NotebookService } from './../notebook/notebook.service';
import { Observable } from 'rxjs/Observable';
import { Component, OnInit } from '@angular/core';
import { SearchService } from './search.service';
import { ActivatedRoute, Router } from "@angular/router";

import 'rxjs/add/operator/map';
import 'rxjs/add/operator/switchMap';
import 'rxjs/add/observable/empty';
import extend from 'lodash-es/extend';
import { Notebook } from '../notebook/notebook';

@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.css']
})
export class SearchComponent implements OnInit {

  searchString: string;
  pagination = {
    currentPage: 1,
    nbHits: 0,
    totalPages: 100,
    hitsPerPage: 0
  };
  results = [];
  notifications = [];
  noData: boolean = true;

  constructor(
    private notebookService: NotebookService,
    private searchService: SearchService,
    private route: ActivatedRoute,
    private router: Router,
    private statisticsService: StatisticsService
  ) { }

  ngOnInit() {
    this.route.queryParams
      .switchMap(params => {
        this.searchString = params['query'];
        if (this.searchString) {
          return this.searchService.getResults(params['query'], params['page']);
        } else {
          return Observable.empty();
        }
      })
      .subscribe(
        data => {
          debugger;
          if (data['nbHits']) {
            this.noData = false;
            this.setPaginationDetails(data);
            this.saveStatistics(data);
            this.handleApiQuotaLimit();
          } else {
            this.noData = true;
            this.results = [];
          }
        },
        err => {
          this.notifications.push({
            type: 'danger',
            msg: `Error occured: ${err.statusText}`
          });
        }
      );
  }

  search() {
    this.router.navigate(['/search'], { queryParams: { query: this.searchString } });
  }

  pageChanged($event) {
    this.router.navigate(['/search'], { queryParams: { query: this.searchString, page: $event.page } });
  }

  private setPaginationDetails(paginationDetails) {
    this.results = paginationDetails.hits;
    extend(this.pagination, paginationDetails);
  }

  private handleApiQuotaLimit() {
    if (this.pagination.nbHits > (this.pagination.hitsPerPage * this.pagination.totalPages)) {
      this.notifications.push({
        type: 'warning',
        msg: 'You have exceeded a query limit. Provide more detailed search criteria. Result set will be trimmed to 1000.'
      });
    }
  }

  private saveStatistics(data) {
    this.statisticsService.add({
      query: this.searchString,
      nbHits: data.nbHits
    });
  }

}



