import { Query } from './query';
import { StatisticsService } from './statistics.service';
import { Component, OnInit } from '@angular/core';

import map from 'lodash-es/map';
import reduce from 'lodash-es/reduce';
import filter from 'lodash-es/filter';
import groupBy from 'lodash-es/groupBy';

@Component({
  selector: 'app-statistics',
  templateUrl: './statistics.component.html',
  styleUrls: ['./statistics.component.css']
})
export class StatisticsComponent implements OnInit {

  statisticList =[];

  constructor(
    private statisticsService: StatisticsService
  ) { }

  ngOnInit() {
    this.statisticList = this.prepareStatistics(this.statisticsService.queriesList);
  }

  private prepareStatistics(stats) {
    const data = groupBy(stats, "query");
    
    const now = new Date();
    const yestrday = new Date(now.setDate(now.getDate()-1));
    const weekBefore = new Date(now.setDate(now.getDate()-7));
    const avg = this.avgForDay;
    
    return map(data, function(value, key){
      return {
        query: key,
        lastDay: avg(value, yestrday),
        lastWeek: avg(value, weekBefore)
      };
    });
    
  }

  private avgForDay(stats, day): number {
    const dayResults = filter(stats, (search: Query) => new Date(search.created) > day);
    const daySum = reduce(dayResults, (sum: number, n: Query) => sum + n.nbHits, 0);
    return Math.floor(daySum / dayResults.length); 
  }

}



