import { StatisticsService } from './statistics/statistics.service';
import { NotebookService } from './notebook/notebook.service';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';

import { PaginationModule } from 'ng2-bootstrap/pagination';
import { AlertModule } from 'ng2-bootstrap/alert';
import { BsDropdownModule } from 'ng2-bootstrap';

import { AppComponent } from './app.component';
import { HomeComponent } from './home/home.component';
import { NotebookEditComponent } from './notebook/notebook-edit/notebook-edit.component';
import { NotebookListComponent } from './notebook/notebook-list/notebook-list.component';
import { NotebookViewComponent } from './notebook/notebook-view/notebook-view.component';
import { SearchComponent } from './search/search.component';
import { AppRoutingModule } from './app-routing.module';
import { SearchService } from './search/search.service';
import { SearchResultViewComponent } from './search/search-result-view/search-result-view.component';
import { StatisticsComponent } from './statistics/statistics.component';

@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    NotebookEditComponent,
    NotebookListComponent,
    NotebookViewComponent,
    SearchComponent,
    SearchResultViewComponent,
    StatisticsComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    AppRoutingModule,
    PaginationModule.forRoot(),
    AlertModule.forRoot(),
    BsDropdownModule.forRoot()
  ],
  providers: [
    SearchService,
    NotebookService,
    StatisticsService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



