import { Query } from './query';
import { Injectable } from '@angular/core';

@Injectable()
export class StatisticsService {

  queriesList: Query[] = [];

  constructor() {
    this.loadDataFromStorage();
  }

  add(query: Query) {
    this.queriesList.push({
      query: query.query,
      created: new Date(),
      nbHits: query.nbHits
    });
    this.updateStorage();
  }

  private updateStorage() {
      localStorage.setItem('HN.SearchQueries', JSON.stringify(this.queriesList));
  }

  private loadDataFromStorage() {
      const queriesListInLocalStorage = JSON.parse(localStorage.getItem('HN.SearchQueries'))
      if (queriesListInLocalStorage) {
        this.queriesList = queriesListInLocalStorage;
      }
  }

}



