import { SearchResult } from './../../search/searchResult';
import { ActivatedRoute } from '@angular/router';
import { NotebookService } from './../notebook.service';
import { Component, OnInit } from '@angular/core';
import { Notebook } from "../notebook";

import find from 'lodash-es/find';
import pull from 'lodash-es/pull';

@Component({
  selector: 'app-notebook-view',
  templateUrl: './notebook-view.component.html',
  styleUrls: ['./notebook-view.component.css']
})
export class NotebookViewComponent implements OnInit {

  notebook: Notebook;

  constructor(
    private notebookService: NotebookService,
    private route: ActivatedRoute
  ) { }

  ngOnInit() {
    this.route.params
      .map(params => +params['id'])
      .subscribe(notebookId => {
        this.notebook = find(this.notebookService.notebooksList, {id: notebookId});
      });
  }

  removeSearchResult(item: SearchResult) {
    pull(this.notebook.searchResults, item);
    this.notebookService.update();
  }

}



