import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router'
import { AppComponent } from './app.component';
import { NotebookListComponent } from './notebook/notebook-list/notebook-list.component';
import { NotebookViewComponent } from './notebook/notebook-view/notebook-view.component';
import { NotebookEditComponent } from './notebook/notebook-edit/notebook-edit.component';
import { SearchComponent } from './search/search.component';
import { HomeComponent } from './home/home.component';

const routes: Routes = [
    { path: '', redirectTo: '/home', pathMatch: 'full' },
    { path: 'home', component: HomeComponent },
    { path: 'notebooks', component: NotebookListComponent },
    { path: 'notebooks/new', component: NotebookEditComponent },
    { path: 'notebooks/:id', component: NotebookViewComponent },
    { path: 'notebooks/:id/edit', component: NotebookEditComponent },
    { path: 'search', component: SearchComponent }
];


@NgModule({
    imports: [ RouterModule.forRoot( routes, { useHash: true }) ],
    exports: [ RouterModule ]
})
export class AppRoutingModule {}


