import { SearchResult } from './../search/searchResult';
import { Injectable } from '@angular/core';
import { Notebook } from './notebook';

import pull from 'lodash-es/pull';

@Injectable()
export class NotebookService {

  notebooksList: Notebook[] = [];

  constructor() {
    this.loadDataFromStorage();
  }

  create(nb: Notebook) {
    nb.id = this.notebooksList.length;
    nb.creationDate = new Date();
    nb.searchResults = [];
    this.notebooksList.push(nb);
    this.updateStorage();
  }

  edit(nb: Notebook) {
    this.updateStorage();
  }

  delete(nb: Notebook) {
    pull(this.notebooksList, nb);
    this.updateStorage();
  }

  update() {
    this.updateStorage();
  }

  private updateStorage() {
      localStorage.setItem('HN.SearchNotebook', JSON.stringify(this.notebooksList));
  }

  private loadDataFromStorage() {
      const notebookInLocalStorage = JSON.parse(localStorage.getItem('HN.SearchNotebook'))
      if (notebookInLocalStorage) {
        this.notebooksList = notebookInLocalStorage;
      }
  }

}



